.team-item {
    text-align: center;
    background-color: transparent;
    -webkit-transition: all 0.25s ease-in-out;
    -moz-transition: all 0.25s ease-in-out;
    -ms-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
}

.team-item:hover {
    background: #3956de;
    color: white;
}

.team-item:hover .team-name,
.team-item:hover .team-kel,
.team-item:hover .team-nim,
.team-item:hover .team-email {
  color: white;
}

.team-info::after {
    background: -webkit-linear-gradient(135deg, #6b02ff 0%, #985bef 100%);
    background: -o-linear-gradient(135deg, #6b02ff 0%, #985bef 100%);
    background: -moz-linear-gradient(135deg, #6b02ff 0%, #985bef 100%);
    background: linear-gradient(135deg, #6b02ff 0%, #985bef 100%);

    display: inline-block;
    vertical-align: middle;
    content: '';
    width: 120px;
    height: 3px;
    margin-top: -170px;
}

.team-item:hover .team-info::after {
    background: -webkit-linear-gradient(135deg, #ff9292 0%, #ff0303 100%);
    background: -o-linear-gradient(135deg, #ff9292 0%, #ff0303 100%);
    background: -moz-linear-gradient(135deg, #ff9292 0%, #ff0303 100%);
    background: linear-gradient(135deg, #ff9292 0%, #ff0303 100%);
}

.team-img {
    max-width: 140px;
    padding: 3px 3px;
    background-color: #dedede;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
}

.team-email {
    font-size: small;
}

@media screen and (max-width: 1024px) {
    .team-email {
        font-size: 10px;
    }

    .team-name {
        font-size: 16px;
    }

    .team-kel {
        font-size: 15px;
    }

    .team-nim {
        font-size: 15px;
    }

    .team-info::after {
        margin-top: -150px;
    }
}

@media screen and (max-width: 768px) {
    .team-email {
        font-size: 11px;
    }

    .team-name {
        font-size: 16px;
    }

    .team-kel {
        font-size: 15px;
    }

    .team-nim {
        font-size: 15px;
    }

    .team-info::after {
        margin-top: -155px;
    }
}

@media screen and (max-width: 480px) {
    .team-email {
        font-size: 13px;
    }

    .team-name {
        font-size: 18px;
    }

    .team-kel {
        font-size: 16px;
    }

    .team-nim {
        font-size: 16px;
    }

    .team-info::after {
        margin-top: -165px;
    }
}

@media screen and (max-width: 375px) {
    .team-email {
        font-size: 10px;
    }

    .team-name {
        font-size: 15px;
    }

    .team-kel {
        font-size: 15px;
    }

    .team-nim {
        font-size: 15px;
    }

    .team-info::after {
        margin-top: -150px;
    }
}

@media screen and (max-width: 320px) {
    .team-email {
        display: none;
    }

    .team-name {
        font-size: 12px;
    }

    .team-kel {
        font-size: 13px;
    }

    .team-nim {
        font-size: 13px;
    }

    .team-info::after {
        margin-top: -105px;
        width: 100px;
    }
}